C;MkB3DF;QAK+B,iBAAgB,EAC9C,EAAA;EAED;IACE,cAAa;IACb,kBAAiB;IACjB,kBAAiB;IACjB,eAAc,EAKf;IlB0BC;MkBnCF;QAMI,eAAc;QACd,+BAA8B,EAEjC,EAAA;EAED;IAAwB,oBhBfb,EgBeuC;EAElD;IACE,cAAa,EAWd;IAZA;MAGS,gBAAe,EAAI;IlB+B3B;MkBlCF;QAMI,cAAa;QACb,+BAA8B;QAC9B,cAAa,EAIhB,EAAA;IlB0BC;MkBtCF;QAU+B,cAAa,EAE3C,EAAA","file":"main.scss","sourcesContent":["\r\n\r\n//MEDIA QUERY MANAGER\r\n\r\n/*\r\n    0-600px: Phone\r\n    600-900px: Tablet portrait\r\n    900-1200px: Tablet landscape\r\n    [1200-1800]: is where our normal styles apply\r\n    1800px + : BIg desktop\r\n\r\n\r\n    $breakepoint argument choices\r\n    -phone\r\n    -tab-port\r\n    -tab-land\r\n    -big-desktop\r\n\r\n    ORDER: Base + typography > general layout + grid > page layout > components\r\n    1em = 16px;\r\n*/\r\n@mixin center-content {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n@mixin maring-vertical ($size) {\r\n  @if $size == big {\r\n    padding: 4.8rem 0;\r\n  }\r\n\r\n  @if $size == large {\r\n    padding: 3.2rem 0;\r\n  }\r\n\r\n  @if $size == medium {\r\n    padding: 2.4rem 0;\r\n  }\r\n}\r\n\r\n@mixin respond ($breakepoint) {\r\n\r\n  @if $breakepoint == xs-phone {\r\n    @media (min-width: 22.5em) { @content };  //320px\r\n  }\r\n\r\n  @if $breakepoint == s-phone {\r\n    @media (min-width: 30em) { @content };  //480px\r\n  }\r\n\r\n  @if $breakepoint == phone {\r\n    @media (min-width: 37.5em) { @content };  //600px\r\n  }\r\n\r\n  @if $breakepoint == xs-tab {\r\n    @media (min-width: 48em) { @content };  //768px\r\n  }\r\n\r\n  @if $breakepoint == tab-port {\r\n    @media (min-width: 56.25em ) { @content }; //900\r\n  }\r\n\r\n  @if $breakepoint == tab-land {\r\n    @media (min-width: 75em ) { @content }; //1200\r\n  }\r\n\r\n  @if $breakepoint == big-desktop  {\r\n    @media (min-width: 112.5em) { @content }; //1800\r\n  }\r\n}",".u-hide-scroll {\r\n  @media(max-width: 1199px) {\r\n    overflow-y: hidden;\r\n  }\r\n}\r\n\r\n.u-rotate-num {\r\n  @include center-content;\r\n  font-size: 1.5rem;\r\n  padding-left: $s-size;\r\n  transform: rotate(-90deg);\r\n  width: 2.5rem;\r\n}","//COLORS\r\n$color-primary: #333;\r\n$color-secondary: #efa047;\r\n$color-tertiary: #ededed;\r\n\r\n$color-white: #fff;\r\n$color-black: #000;\r\n\r\n//SPACING\r\n$s-size: 1rem;\r\n$m-size: 1.6rem;\r\n$l-size: 2.4rem;\r\n$xl-size: 3.2rem;\r\n$xxl-size: 4.8rem;\r\n\r\n//FONT\r\n$default-fs: 1.6rem;\r\n","*,\r\n*::after,\r\n*::before {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: inherit;\r\n} \r\nhtml {\r\n  font-size: 62.5%;\r\n  box-sizing: border-box; \r\n}\r\n\r\nbody {\r\n  box-sizing: border-box;\r\n  @include respond(s-phone) {padding: 0 2rem 0 2rem;}\r\n  @include respond(xs-tab) {padding: 0 ;}\r\n}\r\n\r\n\r\n\r\n","body {\r\n  color: #333;\r\n  font-family: 'Work Sans', sans-serif;\r\n  font-weight: 400;\r\n  line-height: 1.7;\r\n}\r\n\r\n.headline {\r\n  @include center-content;\r\n  @include maring-vertical(big);\r\n  font-size: 2.5rem;\r\n  line-height: 1;\r\n  \r\n\r\n  &__num {\r\n    @include center-content;\r\n    font-size: 1.5rem;\r\n    padding-left: $s-size;\r\n    transform: rotate(-90deg);\r\n    width: 2.5rem;\r\n  }\r\n\r\n  &__text::after {\r\n    background-color: white;\r\n    content: \"\";\r\n    display: block;\r\n    height: .3rem;\r\n    margin-top: $s-size;\r\n    transition: .2s ease-in-out;\r\n    width: 6rem;\r\n    @include respond (tab-port) { width:0; }\r\n  }\r\n\r\n  &__text--sidebar::after {\r\n    width: 100%;\r\n    background-color: $color-white;\r\n    @include respond (phone) { background-color: $color-primary; }\r\n    @include respond (tab-land) { width: 0;}\r\n\r\n  }\r\n\r\n  &__text:hover::after { \r\n    @include respond (tab-port) { width: 7rem;} \r\n  }\r\n  \r\n  &__text--sidebar:hover::after {\r\n    @include respond (tab-land) {\r\n      background-color: $color-white;\r\n      width: 100%; \r\n    } \r\n  }\r\n  \r\n}\r\n\r\n",".hamburger-box {\r\n  position: relative;\r\n  width: 3rem;\r\n  height: 2.5rem;\r\n  z-index: 10;\r\n\r\n  @include respond(xs-tab) {\r\n    flex: 1 1 auto;\r\n    display: flex;\r\n    flex-flow: column;\r\n    align-items: center;\r\n  }\r\n}\r\n\r\n.hamburger {\r\n  position: absolute;\r\n  width: 3rem;\r\n  height: 3px;\r\n  background-color: $color-white;\r\n  backface-visibility: hidden;\r\n  transition: all .3s ease-in-out;\r\n  @include respond(phone) { background-color: $color-primary; }\r\n\r\n  &--top { \r\n    backface-visibility: hidden;\r\n    top: 0%;\r\n\r\n    &-active {\r\n      backface-visibility: hidden;\r\n      top:50%;\r\n      transform-origin: center;\r\n      transform: translateY(-50%) rotate(225deg);\r\n      @include respond(tab-land) { background-color: $color-white; }\r\n\r\n    }\r\n  }\r\n\r\n  &--middle {\r\n    backface-visibility: hidden;\r\n    opacity: 1;\r\n    top: 50%;\r\n    transform: translateY((-50%));\r\n    transition: .1s ease .2s;\r\n\r\n    &-active {\r\n      backface-visibility: hidden;\r\n      opacity: 0;  transform:rotate(-45deg);\r\n      @include respond(tab-land) { background-color: $color-white; }\r\n\r\n    }\r\n  }\r\n\r\n  &--bottom { \r\n    backface-visibility: hidden;\r\n    bottom: 0%;\r\n\r\n    &-active {\r\n      backface-visibility: hidden;\r\n      transform-origin: center;\r\n      bottom:50%;\r\n      transform: translateY(50%) rotate(-225deg);\r\n      @include respond(tab-land) { background-color: $color-white; }\r\n\r\n    }\r\n  }\r\n}",".btn {\r\n  border-radius: .3rem;\r\n  border: none;\r\n  cursor: pointer;\r\n  font-size: 1.2rem;\r\n  font-weight: 700;\r\n  font-family: inherit;\r\n  letter-spacing: .8px;\r\n  outline: none;\r\n  padding: 1rem 1.5rem;\r\n  text-decoration: none;\r\n  text-transform: uppercase;\r\n  \r\n  \r\n  &--download {\r\n    border: 2px solid $color-primary;\r\n    color: $color-primary;\r\n  }\r\n\r\n\r\n  &--submit {\r\n    align-self: flex-end;\r\n    background-color: $color-primary;\r\n    border: 2px solid currentColor;\r\n    color: #f7f7f7;\r\n    margin: $xxl-size 0;\r\n  }\r\n}\r\n",".project {\r\n  padding-bottom: $xxl-size;\r\n  width: 100%; \r\n  background-color: $color-tertiary;\r\n  @include respond (tab-port) {\r\n    margin: 0;\r\n    padding: 0 2rem;\r\n    flex: 0 1 50%; \r\n  }\r\n  \r\n  // &__img-box {\r\n  //   width: 100%;\r\n  //   margin: 0 auto;\r\n  // }\r\n  \r\n  &__img {\r\n    background-color: #f7f7f7;\r\n    width: 100%;\r\n    height: 100%;\r\n    margin-bottom: $l-size;\r\n  }\r\n\r\n  &__info {\r\n    font-size: 1.2rem;\r\n    & > * { margin-bottom: 1.5rem;}\r\n  }\r\n}",".skills {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  @include respond(phone) { justify-content: space-evenly;}\r\n  @include respond(tab-port) {\r\n    flex: 0 1 27%; //20\r\n    flex-flow: column;\r\n    justify-content: space-between;\r\n  }\r\n\r\n  &__col {\r\n    padding: 0 $l-size;\r\n    @include respond(s-phone)  { padding: 0 $xl-size;}\r\n    @include respond(tab-port) { \r\n      margin-bottom: $l-size; \r\n      padding:0; \r\n    }\r\n  }\r\n\r\n  &__title { font-size: 2rem; }\r\n\r\n  &__list-group { list-style-type: none; }\r\n\r\n  &__list-item {\r\n    font-size: 1.6rem;\r\n    line-height: 2;\r\n  }\r\n\r\n  &__list-item::after {\r\n    content: \"\";\r\n    display: block;\r\n    height: 1.5px;\r\n    width: 100%;\r\n    padding-bottom: 2px;\r\n    background-color: $color-primary;\r\n    @include respond(tab-port) { width: 80%; }\r\n  }\r\n}",".form {\r\n  margin-top: $xxl-size;\r\n  display: flex;\r\n  flex-flow: column;\r\n\r\n  &__input-box,\r\n  &__textarea-box {\r\n    position: relative;\r\n    display: flex;\r\n  }\r\n\r\n\r\n  &__label {\r\n    font-size: 1.6rem;\r\n    position: absolute;\r\n    top:.3rem;\r\n    left:0;\r\n    line-height: 1;\r\n    z-index: 10;\r\n  }\r\n\r\n  &__input {\r\n    padding-left: 5.3rem;\r\n    margin-bottom: $xl-size;\r\n    &--email { padding-left: 6rem;}\r\n  }\r\n\r\n  &__input,\r\n  &__textarea {\r\n    flex:1;\r\n    color: #f7f7f7;\r\n    background-color: $color-primary;\r\n    outline: none;\r\n    border: none;\r\n    border-bottom: 1px solid currentColor;\r\n    font-family: 'Lato';\r\n    font-size: 1.6rem;\r\n\r\n  }\r\n\r\n  &__textarea-box { flex-flow: column; }\r\n  \r\n  &__textarea {\r\n    height: 2rem;\r\n    padding-left: 8rem;\r\n    -webkit-appearance: none;\r\n    resize: vertical;\r\n\r\n    &:active {\r\n      margin-top: $l-size;\r\n    }\r\n\r\n    &:focus {\r\n      height: 4rem;\r\n      border: 1px solid currentColor;\r\n      padding-left: 2px;\r\n      margin-top: $l-size;\r\n    }\r\n  }\r\n}\r\n\r\n",".footer {\r\n  @include maring-vertical(medium);\r\n  @include respond (phone) { @include maring-vertical(big); }\r\n\r\n  &__contact {\r\n    display: flex;\r\n    flex-flow: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-weight: 1.2rem;\r\n    @include respond (xs-phone) { flex-flow: row wrap; }\r\n  \r\n    & > * {\r\n      font-weight: 500;\r\n      font-size: 1.4rem;\r\n      font-family: inherit;\r\n    }\r\n  }\r\n\r\n  &__line {\r\n    width: 30%;\r\n    background-color: $color-primary;\r\n    height: 2px;\r\n    margin: 1rem 0;\r\n    \r\n    @include respond(s-phone) { \r\n      flex: 0 1 15%;\r\n      margin: 0;  \r\n    }\r\n  }\r\n\r\n  &__phone,\r\n  &__email { \r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: center;\r\n    padding: 0 1rem;\r\n    @include respond(s-phone) {\r\n      flex: 0 1 40.5%; \r\n      padding: 0 .5rem;\r\n      justify-content: flex-end;\r\n    }\r\n    @include respond(phone) { flex: 0 1 40%; }\r\n    @include respond(xs-tab) { padding: 0 2rem; }\r\n  }\r\n\r\n  &__email { @include respond(s-phone) { justify-content: flex-start; } }\r\n\r\n  &__logo-box {\r\n    flex: 0 1 20%;\r\n    display: flex;\r\n    justify-content: center;\r\n    margin: $m-size 0;\r\n    \r\n    @include respond(phone) { flex: 0 1 20%;}\r\n    @includ